*{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
body,html{
    scroll-behavior: smooth;
}
.container{
    max-width:1536px;
    margin-left:auto;
    margin-right:auto;
    padding-left:0.5rem;
    padding-right:0.5rem;
}

@media(max-width:1536px){
    .container{
        max-width: 1280px;
    }
}
@media(max-width:1280px){
    .container{
        max-width: 1024px;
    }
}
@media(max-width:1024px){
    .container{
        max-width: 768px;
    }
}
@media(max-width:768px){
    .container{
        max-width: 640px;
    }
}
@media(max-width:640px){
    .container{
        max-width: 475px;
    }
}
@media(max-width:475px){
    .container{
     width: 100%;
    }
}